<template>
  <div>
    <v-sidebar :treeData=treeData v-on:childByValue="childByValue"></v-sidebar>
    <div class="content-box" :class="{'content-collapse':collapse,minPaddingLeft:minPaddingLeft,maxPaddingLeft:maxPaddingLeft}">
      <div class="content contentselfdefine">
        <div class="wrap">
          <transition name="move" mode="out-in">
            <keep-alive :include="tagsList">
              <router-view></router-view>
            </keep-alive>
          </transition>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
import vHead from "../../common/Header.vue";
import vSidebar from "../../common/Sidebar.vue";
import vTags from "../../common/Tags.vue";
import bus from "../../common/bus";
export default {
  data() {
    return {
      tagsList: [],
      treeData: [
        {
          icon: "el-icon-dn",
          index: "energyview",
          title: "企业能效分析",
          subs: [
            {
              index: "energyview",
              title: "能源看板"
            },
            {
              index: "metrotrend",
              title: "计量趋势分析"
            },
            {
              index: "metrocompare",
              title: "计量对比分析"
            },
            {
              index: "deviceconsum",
              title: "设备能耗分析"
            },
            {
              index: "Workshopcost",
              title: "车间成本分析"
            },
            {
              index: "StandardAnalysis",
              title: "单耗对标分析"
            }
          ]
        },
//        {
//          icon: "el-icon-dn",
//          index: "PVview",
//          title: "多能供需预测",
//          subs: [
//            {
//              index: "PVforecast",
//              title: "光伏功率预测"
//            }
//          ]
//        },
        {
          icon: "el-icon-gw",
          index: "stationWebNetwork",
          title: "多能管网"
        },
        {
          icon: "el-icon-sc",
          index: "stationWebProduce",
          title: "多能生产"
        },
        {
          icon: "el-icon-jl",
          index: "stationWebMonitor",
          title: "能源计量"
        },
        {
          icon: "el-icon-xf",
          index: "stationWebDemand",
          title: "多能消费"
        },
        {
          icon: "el-icon-gj",
          index: "userWebAlarm",
          title: "告警信息"
        },
        {
          icon: "el-icon-gj",
          index: "userWebPortal",
          title: "用户首页"
        },
        {
          icon: "el-icon-gj",
          index: "userWebMonitor",
          title: "运行监视"
        },
        {
          icon: "el-icon-gj",
          index: "userWebAnalysis",
          title: "数据分析"
        },
        {
          icon: "el-icon-gj",
          index: "userWebHistory",
          title: "历史查询"
        }
      ],
      collapse: false,
      minPaddingLeft: false,
      maxPaddingLeft: true
    };
  },

  methods: {
    childByValue: function(childValue) {
      console.log("childValue:"+childValue);
      var windowWidth = window.innerWidth;
      // if (windowWidth >= 1366) {  //笔记本上网页宽度小于1366，组件收缩效果会出问题
        if (childValue) {
          // childValue就是子组件传过来的值
          this.minPaddingLeft = childValue;
          this.maxPaddingLeft = !childValue;
        } else {
          this.minPaddingLeft = childValue;
          this.maxPaddingLeft = !childValue;
        }
      // } else {
      //   if (childValue) {
      //     // childValue就是子组件传过来的值
      //     this.minPaddingLeft = !childValue;
      //   } else {
      //     console.log(childValue);
      //     this.minPaddingLeft = !childValue;
      //   }
      // }
    }
  },
  components: {
    vHead,
    vSidebar,
    vTags
  },
  created() {
    //  bus.$emit("menuitem", 0);
    var windowWidth = window.innerWidth;
    if (windowWidth >= 1366) {
      this.maxPaddingLeft = true;
    }
    bus.$on("collapse", msg => {
      this.collapse = msg;
    });

    // 只有在标签页列表里的页面才使用keep-alive，即关闭标签之后就不保存到内存中了。
    bus.$on("tags", msg => {
      let arr = [];
      for (let i = 0, len = msg.length; i < len; i++) {
        msg[i].name && arr.push(msg[i].name);
      }
      this.tagsList = arr;
    });
  },
  mounted() {

  }
};
</script>

<style>
.content.contentselfdefine{padding:15px}
</style>



